@import url(https://fonts.googleapis.com/css2?family=Londrina+Sketch&display=swap);
body {
  margin: 0;
  font-family: 'Londrina Sketch', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/tic-tac-toe-react/static/media/paperback.06545513.jpg);
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 600px) {
  body {
    background: url(/tic-tac-toe-react/static/media/paper.c53b854b.jpg);
    background-size: 100vh;
  }
}
.App {
  text-align: center;
  display: grid;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gameboard {
  display: grid;
  place-items: center;
  margin: 0 auto;
  width: 100%;
}

.gameboard h1 {
  font-size: 4rem;
}

.gameboard__announcement {

}

.gameboard__tiles-section {
  width: 80%;
  max-width: 500px;
}

.gameboard__tiles-container {
  padding-top: 100%;
  position: relative;
}

.gameboard__tiles {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  place-items: center;
  grid-gap: 10px;
}

.gameboard__scoreboard-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .gameboard {
    grid-gap: 30px;
  }

  .gameboard h1 {
    font-size: 3rem;
  }
}

.tile {
  display: grid;
  place-items: center;
  border: 1px dashed gray;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  font-size: 3rem;
}

.tile:hover {
  background-color: rgba(135, 206, 250, 0.3);
  transition: background-color 0.3s ease-out;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .tile:hover {
    background-color: #fff;
  }
}
.startbtn {
  margin: 20px auto;
}

.startbtn button {
  font-size: 3rem;
  font-family: 'Londrina Sketch', cursive;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid white;
  background:
    linear-gradient(
      to right, 
      rgba(240, 128, 128, 0.1) 0%,
      rgba(240, 128, 128, 0.1) 100%
    );
  background-position: left;
  background-size: 0 0;
  background-repeat: repeat-y;
  transition: all 0.3s ease-out;
}

.startbtn button:hover {
  cursor: pointer;
  background-size: 100% 1px;
  border: 1px solid lightcoral;
}

.startbtn button:focus {
  outline: none;
}

@media only screen and (max-width: 600px) {
  .startbtn button {
    border: none;
    font-size: 1.5rem;
  }
}
.scoreboard {
  font-size: 3rem;
}

@media only screen and (max-width: 600px) {
  .scoreboard {
    font-size: 1.5rem;
  }
}
.winner-modal {
  position: absolute;
  left: 0;
  right: 0;
  top: 5rem;
  bottom: 45rem;
  display: grid;
  place-items: center;
  /* pointer-events: none; */
  background: rgba(255, 255, 255, 0.9)
}

.winner-modal__box {
  height: 100px;
  width: 100%;
  font-size: 1.5rem;
  border-radius: 10px;
  /* background: rgba(224, 255, 255, 0.8); */
  display: grid;
  place-items: center;
}

@media only screen and (max-width: 600px) {
  .winner-modal {
    top: 0;
    bottom: 0;
  }
}

